  /* -----------------------NAVIGATE --------------*/
.navigate {
  font-weight: 500;
  line-height: 10px;
  text-transform: uppercase;
  font-family: 'Ubuntu', sans-serif; ;
  font-size: 12px;
  color: #fff;
  position: fixed;
  width: 100%;
  left: 0;
  bottom: 0;
  z-index: 50;
  height: 40px;
}
.navigate__body{
  height: 50px;
}
.navigate__menu{
  position: fixed;
  left: 0;
  top: 100%;
  width: 100%;
  height: 100%;
  background-color: #17101c;
  transition: all 0.3s ease-in;
  overflow: auto;
}
.navigate__list{
  margin-top: 30px;
  display: block;
  padding-left: 30px;
}
.navigate__list li+*{
  margin-top: 20px;
  padding-left: 0px;
}
.navigate::before{
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #17101c;
  z-index: 2;
}
.navigate__logo {
  position: absolute;
  top: 10px;
  left: 10px;
  z-index: 3;
}

.App.lock{
  overflow: hidden;
}
.arrow-menu{
  opacity: 0;
}
@media screen and (min-width:800px) {
  .arrow-menu{
    font-size: 10px;
    opacity: 1;
    padding-left: 5px;
  }
  .navigate::before{
    background-color: transparent;
  }
  .navigate{
    position: relative;
  }
    .navigate__list{
      margin-top: 0;
    }
    .navigate__menu{
      position: relative;
      left: 0;
      top: 0;
      width: 100%;
      height: initial;
      background-color: transparent;
      overflow: visible;
    }
    .navigate__body {
      display: flex;
      justify-content: space-between;
      align-items: center;
  }
  .navigate__logo {
    position: relative;
      top: 0;
      left: 0;   
  }
  .navigate__list {
    display: flex;
    align-items: baseline;
    position: relative;
    z-index: 2;
    margin-left: 30px;
}

  .navigate__list li +* {
    margin-top: 0;
    margin-left: 10px;
  }
  .navigate__list li {
    border-top: 8px solid transparent;
    border-bottom: 3px solid transparent;
  }
  .navigate__list li:hover {
    border-top: 8px solid transparent;
    border-bottom: 3px solid #f14d34;
  }
}
/* -------------------------BURGER----------------- */
.navigate__burger{
  display: block;
  position: absolute;
  width: 30px;
  height: 20px;
  z-index: 3;
  top: 10px;
  right: 10px;
}
.navigate__burger span{
  background-color: #fff;
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 100%;
  height: 2px;
}
.navigate__burger::after,.navigate__burger::before{
  content: "";
  width: 100%;
  height: 2px;
  position: absolute;
  background-color: #fff;
  left: 0;
  right: 0;
}
.navigate__burger::after{
  bottom: 0;
}
.navigate__burger::before{
  top: 0;
}
.navigate__burger.active::before{
  transform: rotate(45deg);
  top: 9px;
}
.navigate__burger.active::after{
  transform: rotate(135deg);
  bottom: 9px;
}
.navigate__burger.active span{
  display: none;
}
.navigate__menu.active{
  top:0;
}

@media screen and (min-width:800px) {  
    .navigate__burger {
        display: none;
    }
}
  
